@import "../../global.scss";
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap');

.contact {
    background-color: $darkGreen;
    position: relative;
    height: 70vh !important;
    overflow-y: hidden;

    h2 {
        font-size: 10%;
        font-weight: 750;

        @media only screen and (min-width: $md) {
            font-size: 6.5vw;
        }

        @media only screen and (min-width: $xxl) {
            font-size: 6vw;
        }


    }

    .textBox {
        margin-top: 8vh;
        z-index: 2;
        position: absolute;
        line-height: 130%;
        color: $creamGreen;
    }

    .big-star {
        position: relative;
        left: 80vw;
        max-height: 300px;

        @media only screen and (min-width: $xxl) {
            left: 66vw;
            min-height: 400px;
        }
    }

    .small-star {
        padding-left: 8%;
        position: absolute;
        bottom: 0;

        @media only screen and (min-width: $md) {
            bottom: -10px;
            padding-left: 0;
            transform: scale(0.95);
        }

        @media only screen and (min-width: $xxl) {
            bottom: 0;
            padding-left: 6%;
        }
    }

    .star-text {
        color: $neonGreen;
    }

    .other-contact {
        // align-items: center;
        // text-align: center;
        // z-index: 1;
        // position: relative;
        // top: 65%;
        


        a {
            color: $creamGreen;
            font-size: 30px;
            font-weight: 650;
            padding: 0 3%;
            transition: 1s ease;

            &:hover {
                
                color: $neonGreen;
                font-weight: 750;
                
            }

            &.star-text {
                color: $neonGreen;
            }
        
        }
    }

    .copyright {
        p {
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 20px;
            left: 0;
            right: 0;
            position: absolute;
            bottom: 10vh;
            align-items: center;
            justify-content: center;
            text-align: center;
            text-transform: uppercase;
            color: $neonGreen;
            font-weight: 600;
            max-width: 70%;
            font-family: 'DM Sans', sans-serif;

            @media only screen and (min-width: 768px) {
                bottom: 0; 
            }
        }
    }

//------------------------------------------------------------------

    hr {
        position: relative;
        color: $creamGreen;
        margin-top: 17vh;
    }
    .contact-info {
        display: flex;
        flex-direction: row;
        font-size: 1.25rem;
        left: 0;
        right: 0;
        position: absolute;
        margin-bottom: 6vh;
        

        @media only screen and (min-width: $md) {
            bottom: 0; 
        }

        p,a {
            font-family: 'DM Sans', sans-serif;
            color: $creamGreen;
        }

        a {
            &:hover {
                color: $neonGreen;
                font-weight: 750;
            }
        }

        .contact-section {
            padding-left: 10%;
        }

        .empty-section {
            width: 30%;

            @media only screen and (min-width: $xxl) {
                width: 40%;
            }
        }
    }
}

